import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import MenuItem from "./components/MenuItem";
import Head from "./components/head";
import Total from "./components/total";
import { useState, useEffect } from "react";
import { renderIntoDocument } from "react-dom/test-utils";
import { Button } from "bootstrap";
import { Table } from "bootstrap";

// import 'bootstrap/dist/css/bootstrap.min.css'; // This imports bootstrap css styles. You can use bootstrap or your own classes by using the className attribute in your elements.

// Menu data. An array of objects where each object represents a menu item. Each menu item has an id, title, description, image name, and price.
// You can use the image name to get the image from the images folder.

function App() {
  const [currentCity, setCity] = useState("Austin");
  const [currentCityTemps, setTemps] = useState([]);
  const [newCity, setNewCity] = useState("");
  const [locationAPICalls, setCities] = useState({
    Austin: {
      url: "https://api.open-meteo.com/v1/forecast?latitude=30.26715&longitude=-97.74306&hourly=temperature_2m&temperature_unit=fahrenheit&forecast_days=1",
      cityName: "Austin",
    },
    Dallas: {
      url: "https://api.open-meteo.com/v1/forecast?latitude=32.78306&longitude=-96.80667&hourly=temperature_2m&temperature_unit=fahrenheit&forecast_days=1",
      cityName: "Dallas",
    },
    Houston: {
      url: "https://api.open-meteo.com/v1/forecast?latitude=29.76328&longitude=-95.36327&hourly=temperature_2m&temperature_unit=fahrenheit&forecast_days=1",
      cityName: "Houston",
    },
  });
  useEffect(() => {
    getWeatherData("Austin");
  }, []);

  async function getWeatherData(city) {
    try {
      const response = await fetch(locationAPICalls[city]["url"]);
      console.log("FETCHED!");
      const json = await response.json();
      console.log(json["hourly"]["temperature_2m"]);
      setCity(city);
      setTemps(json["hourly"]["temperature_2m"]);
    } catch (err) {
      console.log(err);
    }
  }

  async function addCity() {
    try {
      const geoUrl = `https://geocoding-api.open-meteo.com/v1/search?name=${newCity}&count=1&language=en&format=json`;
      const response = await fetch(geoUrl);
      const json = await response.json();
      if (!json["results"]) {
        console.log("ERROR");
        alert("City not found");
        return;
      }
      const long = json["results"]["0"]["longitude"];
      const lat = json["results"]["0"]["latitude"];
      const url = `https://api.open-meteo.com/v1/forecast?latitude=${lat}&longitude=${long}&hourly=temperature_2m&temperature_unit=fahrenheit&forecast_days=1`;
      setCities({
        ...locationAPICalls,
        [newCity]: { url: url, cityName: newCity },
      });
      try {
        const response = await fetch(url);
        console.log("FETCHED!");
        const json = await response.json();
        console.log(json["hourly"]["temperature_2m"]);
        setCity(newCity);
        setTemps(json["hourly"]["temperature_2m"]);
      } catch (err) {
        console.log(err);
      }
      console.log(locationAPICalls);
    } catch (err) {
      console.log(err);
    }
  }

  function renderTables() {
    return currentCityTemps.map((temp, index) => (
      <tr key={index}>
        <td>{index + 1}</td>
        <td>{temp}</td>
      </tr>
    ));
  }
  return (
    <div>
      <section className="container" id="buttons">
        {Object.keys(locationAPICalls).map((city) => (
          <button
            key={city}
            className="btn btn-primary"
            onClick={() => getWeatherData(city)}
          >
            {city}
          </button>
        ))}
        <input
          type="text"
          placeholder="Enter City"
          onChange={(text) => setNewCity(text.target.value)}
        />
        <button type="button" className="btn btn-secondary" onClick={addCity}>
          Add City
        </button>
      </section>
      <section className="container" id="Temps">
        <h1>City: {currentCity}</h1>
        <table className="table">
          <thead>
            <tr>
              <th>Hour</th>
              <th>Temperature</th>
            </tr>
          </thead>
          <tbody>{renderTables()}</tbody>
        </table>
      </section>
    </div>
  );
}
export default App;
